

import "../css/stats.css"
import { useNavigate } from "react-router-dom"


import { signInWithPopup, GoogleAuthProvider } from "firebase/auth";
import { auth } from "../config/firebase.js";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { current_user } from "../store/counterslice";




import { Bar, Pie, Line, Doughnut, Radar, PolarArea, Scatter , Bubble} from "react-chartjs-2";
import Chart from 'chart.js/auto';





const App = () => {


    const count = useSelector(state => state.counter)
    const navigate = useNavigate();
    const dispatch = useDispatch();


    
    console.log(count.cart)


    const userExists = () => {



        var userData = JSON.parse(localStorage.getItem("delivery-user"));

        // console.log(userData)
        userData ?


            dispatch(current_user(userData))




            : console.log("nae hai")







    }


    const google_login = () => {


        const provider = new GoogleAuthProvider();

        signInWithPopup(auth, provider)
            .then((result) => {
                const credential = GoogleAuthProvider.credentialFromResult(result);
                const token = credential.accessToken;

                const user = result.user;


                const obj = { username: user.displayName, photoURL: user.photoURL, uid: user.uid, cart: [] }

                dispatch(current_user(obj))

                localStorage.setItem("delivery-user", JSON.stringify(obj))





            }).catch((error) => {

                const errorCode = error.code;
                const errorMessage = error.message;
                console.log("errorMessage")
                const credential = GoogleAuthProvider.credentialFromError(error);
                // ...
            });
    }




    useEffect(() => { userExists() }, [2])




    const [userdata, setuserdata] = useState({


        labels: ["Olivia", "Hemani", "Dell", "grocery"],

        datasets: [{
            label: "brands", data: [3, 4, 5, 4], backgroundColor: ["orange", "yellow", "pink" , "green"], barPercentage: 0.5,
            barThickness: 20,

            tension: 0.4,

        }]
    })




    return (
        <div className="stats_base">


            <div className="navbar" >


                <img className="logoimg" onClick={() => navigate("/delivery_app")} src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/55/Emart_Logo.svg/1280px-Emart_Logo.svg.png" />

                <span className="nav_inner" >

                    <div className="small_nav_img_box">
                        <img src="https://img.icons8.com/fluency-systems-regular/48/000000/favorite-cart.png" onClick={() => navigate("/delivery_app/cart")} className="cart_img" />  <span className="cartlen" >{count.currentUser.cart.length} </span>
                    </div>



                    <div className="small_nav_img_box">
                        <img src="https://img.icons8.com/fluency-systems-regular/48/000000/laptop-metrics.png" onClick={() => navigate("/delivery_app/stats")} className="cart_img" />
                    </div>



                    {count.currentUser.username == "none" ?

                        <div onClick={() => google_login()} className="small_nav_img_box">
                            <img referrerPolicy="no-referrer" className="small_nav_img" src="https://img.icons8.com/material-outlined/24/000000/user--v1.png" />
                        </div>



                        :

                        <div className="small_nav_img_box">
                            <img referrerPolicy="no-referrer" className="small_nav_img" src={count.currentUser.photoURL} />
                        </div>

                    }
                </span>

            </div>


            <div className="line">

                <div className="ubox uboxone">

                    <span className="l"> <h2>{count.cart.length}</h2> sales</span>

                    <span className="r"><img src="https://img.icons8.com/wired/64/000000/cash-.png" className="fit" /></span>
                </div>


                <div className="ubox uboxtwo">  </div>
                <div className="ubox uboxthree">  </div>
            </div>




            <div className="charts">



                <div className="chart one">


                    <Pie
                        className="actual_bar"
                        data={userdata}



                        height='50vh'
                        width='50vw'
                        options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200, color: "white" }, } } }}

                    />
                </div>





                <div className="chart two">

                    <Bar
                        className="actual_bar"
                        data={userdata}



                        height='50vh'
                        width='50vw'
                        options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 , color: "white"} } } }}

                    />
                </div>



                <div className="chart three">



                    <Doughnut
                        className="actual_bar"
                        data={userdata}




                        options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200, color: "white" } } } }}

                    />

                </div>




                <div className="chart four">

                    <Line
                        className="actual_bar"
                        data={userdata}





                        options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200, color: "white" } } } }}

                    />


                </div>



                <div className="chart five">


                    <PolarArea
                        className="actual_bar"
                        data={userdata}





                        options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 , color: "white"} } } }}

                    />




                </div>


            </div>





        </div>
    )
}



export default App



